export default function ColumnGroupingTable() {
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);
    const navigate = useNavigate();

    const Back = () => {
        navigate('/');
    }

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    const [scoreData, setScoreData] = useState([]);

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_BACKEND}/api/gethistory`)
            .then(res => {
                axios.get(`${process.env.REACT_APP_BACKEND}/NFTData/getUserList`)
                    .then(userlist => {
                        let array = [];
                        userlist.data.forEach(async user => {
                            var winCount = 0;
                            var loseCount = 0;
                            let history = [];
                            for (let index = 0; index < res.data.length; index++) {
                                const element = res.data[index];
                                if (element.address1 === user.address) {
                                    history.push(element);
                                    if (element.score1 >= element.score2) {
                                        winCount++;
                                        console.log(element, user.address, '+++')
                                    } else {
                                        loseCount++;
                                        console.log(element, user.address, '---')
                                    }
                                } else if (element.address2 === user.address) {
                                    history.push(element);
                                    if (element.score1 <= element.score2) {
                                        winCount++;
                                        console.log(element, user.address, '-----')
                                    } else {
                                        loseCount++;
                                        console.log(element, user.address, '-----')
                                    }
                                }
                            }
                            array.push({
                                address: user.address,
                                win: winCount,
                                lose: loseCount,
                                history: history
                            })
                        });
                        array.sort(function (x, y) {
                            return y.win - x.win;
                        });
                        setScoreData(array);
                    })
            })
            .catch(err => {
                console.log(err, 'err')
            })
    }, [!scoreData])

    return (
        <div style={{ display: 'grid', marginTop: '5%' }}>
            <span style={{ textAlign: 'center', marginBottom: '30px', fontWeight: '900', fontSize: '50px', color: '#0089ff' }}>Score Board</span>
            <TableContainer component={Paper} sx={{ width: '80%', margin: '0% 10%', marginBottom: '50px', backgroundColor: 'transparent', border: '1px solid white' }}>
                <Table aria-label="collapsible table">
                    <TableHead>
                        <TableRow>
                            {/* <TableCell /> */}
                            {/* <TableCell align="left" style={{ color: '#00d8fb' }}>Rank</TableCell> */}
                            <TableCell align="left" style={{ color: '#00d8fb' }}>Address</TableCell>
                            <TableCell align="left" style={{ color: '#00d8fb' }}>Win</TableCell>
                            <TableCell align="left" style={{ color: '#00d8fb' }}>Lose</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {scoreData
                            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                            .map((row, counter) => {
                                return (
                                    <Row key={row.address} row={row} counter={counter} />
                                )
                            })}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 50, 100]}
                    component="div"
                    count={scoreData.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                    className='pagenation'
                    style={{ color: '#00d8fb' }}
                />
            </TableContainer>
            <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '20px' }}>
                <button onClick={Back} className="button-82-pushable" role="button">
                    <span className="button-82-shadow"></span>
                    <span className="button-82-edge"></span>
                    <span className="button-82-front text">Back</span>
                </button>
            </div>
        </div>
    );
}