import React, { useState, useEffect } from "react";
import Unity, { UnityContext } from "react-unity-webgl";
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';
import toast, { Toaster } from "react-hot-toast";
import Modal from '@mui/material/Modal';
import { useSelector } from "react-redux";
import { useConnectWallet, useDisconnectWallet } from "../utils/hooks";
import { shortenAddress } from "../utils/stylish";
import ReactLoading from 'react-loading';

const unityContext = new UnityContext({
    loaderUrl: "build/Build/build.loader.js",
    dataUrl: "build/Build/build.data",
    frameworkUrl: "build/Build/build.framework.js",
    codeUrl: "build/Build/build.wasm"
});


function Home() {

    const UserData = useSelector((state) => state.user);

    const ReturenHome = () => {
        setGameResult(false);
        navigate('/home')
    }

    const {
        connectWallet,
        web3,
        address,
        networkId,
        connected
    } = useConnectWallet();

    const nftData = useSelector((state) => state.nft);

    const [open, setOpen] = React.useState(true);

    const navigate = useNavigate();

    const CancelGame = () => {
        unityContext.send("Game", "GameClose", JSON.stringify({
            data: "surrender"
        }))
        toast.error('Canceled Game')
        setGameResult(true);
    }

    const [gameResult, setGameResult] = useState(false);

    useEffect(async () => {

        if (UserData.address !== '') {
            let user = [
                "Player",
                `${process.env.REACT_APP_BACKEND}/bot.png`,
                "address"
            ]
            if (UserData.avatar !== '') {
                user = [];
                user.push(UserData.nickName);
                user.push(`${process.env.REACT_APP_BACKEND}/${UserData.avatar}`,);
                user.push(UserData.address);
            }

            let data = [];

            let filename = []

            if (nftData.data.data !== undefined) {
                nftData.data.data.map((nft) => {
                    filename.push(nft.image);
                    let obj = {
                        Name: `<color=\"#b7fa25\">${nft.name}</color>`,
                        Avatar: `${nft.image}`,
                    }
                    nft.attributes.map(d_nft => {
                        obj[d_nft.trait_type] = d_nft.value
                    })
                    data.push(JSON.stringify(obj))
                })
            }

            var dec = {
                Id: "",
                Name: "Vip",
                Desc: "",
                Avatar: "vip_icon",
                BigAvatar: "vip",
                Cards: []
            }

            for (var i = 0; i < filename.length; i++) {
                dec.Cards.push(filename[i]);
            }

            var decName = dec.Name;

            unityContext.on("GameReady", function (message) {
                if (message == "Ready") {
                    setOpen(false);
                    try {
                        unityContext.send("Game", "RequestToken", JSON.stringify({
                            data: data,
                            fileName: filename,
                            dec: JSON.stringify(dec),
                            decName: decName,
                            user: user
                        }));
                    } catch (err) {
                        console.log(err);
                    }
                }
            });
        } else {
            navigate('/')
        }
    }, [address])

    return (
        <div style={{ width: '100%', height: window.innerHeight + 'px', display: 'grid', justifyContent: 'center' }}>
            <Toaster />
            <div style={{ width: window.innerWidth + 'px', display: 'contents' }}>
                <div style={{ height: window.innerHeight + 'px', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                    <ReactLoading className="mCenter" type='bars' color='hsl(340deg 100% 50%)' height={'40px'} width={'100px'} />
                    <p style={{ fontSize: 'x-large', margin: '80px 0px', color: 'white' }}>Game Loading...</p>
                </div>
                {
                    !open ?
                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <button onClick={CancelGame} className="glow-on-hover" style={{ zIndex: '99', margin: '20px' }} role="button">
                                Cancel Game
                            </button>
                        </div>
                        :
                        <></>
                }
                <Unity
                    unityContext={unityContext}
                    matchWebGLToCanvasSize={true}
                    style={{ height: "100vh", width: '100%', position: 'absolute', left: '0px' }}
                />
            </div>
            <Modal
                className='MMModal'
                open={gameResult}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
                style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
            >
                <div style={{ display: 'grid', width: '1000px', height: '100%', alignItems: 'center' }}>
                    <div style={{ display: 'grid' }}>
                        <div style={{ display: 'flex', width: '100%', justifyContent: 'center' }}>
                            <span className='login_text'>Battle Result</span>
                        </div>
                        <div style={{ marginTop: '20px', display: 'flex', justifyContent: 'center' }}>
                            <Button className="home_ready" style={{ width: '100%', display: 'flex' }}>
                                <span className="mfontSize" style={{ flex: '1', textAlign: 'left', color: 'white', fontWeight: '700', fontSize: 'x-large', marginLeft: '10px' }}>{shortenAddress(address)}</span>
                                <span className="mfontSize" style={{ flex: '2', color: 'black', fontWeight: '700', fontSize: 'x-large', textAlign: 'center' }}>Cancelled The Game</span>
                            </Button>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                            <Button onClick={ReturenHome} className="mfontSize login_button" style={{ margin: '10px' }}>
                                Return Home
                            </Button>
                        </div>
                    </div>
                </div>
            </Modal>
        </div>
    );
}

export default Home

