import React, { useState, useEffect } from 'react';
import ReactImageUploading from 'react-images-uploading';
import {
    Grid,
    Stack,
    Box,
    Container,
    Button,
    Typography,
    IconButton,
    TextField,
} from '@mui/material'

import toast, { Toaster } from 'react-hot-toast';
import axios from 'axios';
import AddAPhotoIcon from '@mui/icons-material/AddAPhoto';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import defaultImage from '../assets/imgs/unknown.png'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom';

export default function AdminAirDrop() {
    const navigate = useNavigate();

    const UserData = useSelector((state) => state.user);
    const [gameAvatar, setGameAvatar] = useState([]);
    const [Address, setAddress] = useState('')
    const [nickName, setNickName] = useState('');
    const [avatarName, setAvatarName] = useState('');

    const onChange = (setTempImage, imageList, addUpdateIndex) => {
        setTempImage(imageList);
    };

    const Register = async () => {
        async function uploadImage() {
            let params = new FormData()
            if (gameAvatar.length !== 0) {
                params.append("userAvatar", gameAvatar[0].file);
            }
            params.append("address", Address);
            params.append("nickName", nickName);
            await axios.post(`${process.env.REACT_APP_BACKEND}/NFTData/UploadImage`, params)
                .then(res => {
                    toast.success('Update Success');
                    if (res.data !== 'ok') {
                        setAvatarName(res.data);
                    }
                })
                .catch(err => {
                    console.log(err, 'error');
                    toast.error(err.message)
                })
        }

        if (((nickName !== '' && Address !== '') && gameAvatar[0]) || ((nickName !== '' && Address !== '') && avatarName !== '')) {
            await uploadImage();
        } else {
            toast.error('Please Input Correct Data')
        }

    }

    const [updateFlag, setUpdateFlag] = useState(false);

    useEffect(async () => {
        if (UserData.address !== '') {
            setAddress(UserData.address)
            setUpdateFlag(!UserData.newUser);
            if (UserData.newUser === false) {
                setNickName(UserData.nickName);
                setAvatarName(UserData.avatar);
            }
        } else {
            navigate('/')
        }
    }, [])

    const Back = () => {
        navigate('/')
    }

    return (
        <Box sx={{ display: 'grid' }}>
            <Toaster />
            <Grid sx={{ m: 'auto', flexGrow: 1, width: '50%', height: window.innerHeight + 'px', display: 'flex', alignItems: 'center' }} className='NewRegister'>
                <Container>
                    <Stack direction={'row'} style={{ display: 'flex', justifyContent: 'center' }}>
                        <ReactImageUploading
                            value={gameAvatar}
                            onChange={
                                (imageList, addUpdateIndex) =>
                                    onChange(
                                        setGameAvatar,
                                        imageList,
                                        addUpdateIndex
                                    )
                            }
                            dataURLKey="data_url"
                        >

                            {({
                                imageList,
                                onImageUpload,
                                onImageUpdate,
                                onImageRemove,
                                dragProps
                            }) => {
                                return (
                                    <Box sx={{ ml: 5 }} {...dragProps}>
                                        {gameAvatar.length === 0 &&
                                            <Box sx={{ mt: 1, width: '200px', height: '200px', border: '0px', borderRadius: '100px' }}>
                                                <img
                                                    src={
                                                        avatarName === '' ?
                                                            defaultImage
                                                            :
                                                            `${process.env.REACT_APP_BACKEND}/${avatarName}`
                                                    }
                                                    style={{
                                                        width: '200px',
                                                        height: '200px',
                                                        borderRadius: '100px',
                                                        marginTop: '8px'
                                                    }}
                                                />
                                            </Box>
                                        }
                                        {gameAvatar.map((image, index) => (
                                            <React.Fragment key={index}>
                                                <img
                                                    src={image.data_url}
                                                    style={{
                                                        width: '200px',
                                                        height: '200px',
                                                        borderRadius: '100px',
                                                        marginTop: '8px'
                                                    }}
                                                />

                                                <Stack mt={2} direction="row" spacing={2} justifyContent={'space-around'} alignItems="center">
                                                    <IconButton aria-label="upload picture" component="span" onClick={() => onImageUpdate(index)} style={{ color: '#0080ff' }}>
                                                        <AddAPhotoIcon />
                                                    </IconButton>
                                                    <IconButton color="error" aria-label="upload picture" component="span" onClick={() => onImageRemove(index)}>
                                                        <DeleteOutlineIcon />
                                                    </IconButton>
                                                </Stack>

                                            </React.Fragment>
                                        ))}

                                        {gameAvatar.length === 0 &&
                                            <IconButton sx={{ mt: 2, color: '#0080ff', textAlign: 'center' }} aria-label="upload picture" component="span" onClick={onImageUpload}>
                                                <AddAPhotoIcon />
                                            </IconButton>
                                        }
                                    </Box>

                                );
                            }}
                        </ReactImageUploading>
                    </Stack>
                    <Stack direction={'row'} sx={{ my: 2 }} justifyContent='space-between' spacing={2}>
                        <TextField
                            className='TAC'
                            id="outlined-basic1"
                            variant="outlined"
                            value={Address}
                            placeholder="wallet address"
                            onChange={(e) => setAddress(e.currentTarget.value)}
                            sx={{ '& label': { color: 'grey !important' }, '& .MuiInputBase-root': { color: 'grey !important' }, '& .MuiOutlinedInput-notchedOutline': { borderColor: 'grey !important' }, flex: '1', }}
                            InputProps={{
                                readOnly: true,
                            }}
                        />
                    </Stack>
                    <Stack direction={'row'} sx={{ my: 2 }} justifyContent='space-between' spacing={2}>
                        <TextField
                            className='TAC'
                            id="outlined-basic1"
                            variant="outlined"
                            value={nickName}
                            placeholder="Game Nick"
                            onChange={(e) => setNickName(e.currentTarget.value)}
                            sx={{ '& label': { color: 'grey !important' }, '& .MuiInputBase-root': { color: 'grey !important' }, '& .MuiOutlinedInput-notchedOutline': { borderColor: 'grey !important' }, flex: '1', textAlign: 'center' }}
                        />
                    </Stack>

                    <div style={{ width: '100%', display: 'flex', justifyContent: 'center', marginBottom: '30px !important' }}>
                        <button className='button-85' onClick={Back} variant='contained'>
                            Back
                        </button>
                        <button className='button-85' onClick={Register} variant='contained'>
                            {
                                updateFlag ?
                                    'Update'
                                    :
                                    'Register'
                            }
                        </button>
                    </div>
                </Container>
            </Grid >
        </Box >
    );
}
