import React, { useCallback, useEffect, useRef, useState } from 'react'
import { shortenAddress } from "../utils/stylish";
import { useConnectWallet, useDisconnectWallet } from "../utils/hooks";
import { createweb3Modal } from "../utils/createweb3Modal";
import { useNavigate } from 'react-router-dom';
import toast, { Toaster } from 'react-hot-toast';
import { setUserData } from '../redux/actions/user';
import { useDispatch } from 'react-redux';
import axios from 'axios';

export default function LoginComponent() {

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const {
        connectWallet,
        web3,
        address,
        networkId,
        connected
    } = useConnectWallet();
    const { disconnectWallet } = useDisconnectWallet();
    const [web3Modal, setModal] = useState(null);

    useEffect(() => {
        setModal(createweb3Modal);
    }, [setModal]);


    const connectWalletCallback = useCallback(async () => {
        connectWallet(web3Modal);
    }, [web3Modal, connectWallet]);

    const disconnectWalletCallback = useCallback(() => {
        disconnectWallet(web3, web3Modal);
    }, [web3, web3Modal, disconnectWallet]);

    const [flag, setFlag] = useState(false);
    const [loginFlag, setloginFlag] = useState(false)

    const goHome = () => {
        navigate('/home');
    }

    const LoginWallet = () => {
        if (connected) {
            disconnectWalletCallback();
        } else {
            setloginFlag(true)
            connectWalletCallback();
        }
    }

    const RegisterNew = () => {
        navigate('/register');
    }

    const ScoreBoard = () => {
        navigate('/ScoreBoard')
    }

    useEffect(async () => {
        if (address) {
            setloginFlag(true)
            axios.post(`${process.env.REACT_APP_BACKEND}/NFTData/getUserData`, {
                address: address
            }).then(res => {
                if (res.data !== 'nothing') {
                    dispatch(setUserData({
                        address: address,
                        newUser: false,
                        nickName: res.data.nickName,
                        avatar: res.data.userAvatar
                    }))
                    setFlag(true);
                } else {
                    dispatch(setUserData({
                        address: address,
                        newUser: true
                    }))
                }
            }).catch(err => {
                console.log(err, 'err');
            })
        }
    }, [address])

    return (
        <div className='loginP' style={{ alignItems: 'center', height: window.innerHeight - 20 }}>

            {
                flag ?
                    <div style={{ display: 'grid' }}>
                        <Toaster />
                        <span className='login_text'>Welcome</span>
                        <span className='login_text'>{shortenAddress(address)}</span>
                        <button onClick={RegisterNew} className="button-85" style={{ margin: '10px 0px' }}>
                            Update Data
                        </button>
                        <button onClick={goHome} className="button-85">
                            Start Now
                        </button>
                    </div>
                    :
                    <div style={{ display: 'grid' }}>
                        <Toaster />
                        {
                            loginFlag && address ?
                                <div style={{ display: 'grid' }}>
                                    <span className='login_text'>Welcome</span>
                                    <span className='login_text'>{shortenAddress(address)}</span>
                                    <button onClick={RegisterNew} className="button-85" style={{ margin: '10px 0px' }}>
                                        Register New
                                    </button>
                                    <button onClick={goHome} className="button-85">
                                        Start Now
                                    </button>
                                </div>
                                :
                                <div style={{ display: 'grid' }}>
                                    <button className="button-85" onClick={LoginWallet}>
                                        Login
                                    </button>
                                    <button className="button-85" onClick={ScoreBoard} style={{ marginTop: '10px' }}>
                                        ScoreBoard
                                    </button>
                                    <a href='https://www.exmoc.com/blog/1123' className='troubleLink' target='_blank'>Trouble logging in?</a>
                                </div>
                        }
                    </div>
            }
        </div>
    )
}
