// Import Constants
import {
    Nfts
} from '../../constants'

// Initial States
const initialState = {
    data: []
}

const nftReducer = (state = initialState, action) => {
    switch (action.type) {

        case Nfts:
            return {
                ...state,
                data: action.data
            }
        default:
            return state
    }
}

export default nftReducer