// Import Constants
import {
    Users
} from '../../constants'

// Initial States
const initialState = {
    address: '',
    newUser: true,
    nickName: '',
    avatar: ''
}

const userReducer = (state = initialState, action) => {
    switch (action.type) {

        case Users:
            return {
                ...state,
                address: action.data.address,
                nickName: action.data.nickName,
                avatar: action.data.avatar,
                newUser: action.data.newUser,
            }

        default:
            return state
    }
}

export default userReducer