import React, { useCallback, useEffect, useState } from 'react'
import { useConnectWallet, useDisconnectWallet } from "../utils/hooks";
import { createweb3Modal } from "../utils/createweb3Modal";
import { useNavigate } from 'react-router-dom';
import { getTokenContractInstance } from "../utils/web3";
import toast, { Toaster } from 'react-hot-toast';
import { useDispatch, useSelector } from 'react-redux';
import { setNftData } from '../redux/actions/nft'
import { useMoralisWeb3Api, useMoralis } from "react-moralis";
import axios from 'axios';
import { Card, CardActionArea, CardMedia, Checkbox } from '@mui/material';
import { Box } from '@mui/system';
import ReactLoading from 'react-loading';

export default function LoginComponent() {
    const dispatch = useDispatch();
    const Moralis = useMoralis();
    const Web3Api = useMoralisWeb3Api();
    const navigate = useNavigate();
    const label = { inputProps: { 'aria-label': 'Checkbox demo' } };
    const UserData = useSelector((state) => state.user);
    const [nftDatas, setNftDatas] = useState([]);
    const [web3Modal, setModal] = useState(null);
    const [balance, setBalance] = useState(0);
    const [searchFlag, setSearchFlag] = useState(true)
    const [nftlist, setNftList] = useState([]);

    const {
        connectWallet,
        web3,
        address,
        networkId,
        connected
    } = useConnectWallet();

    useEffect(() => {
        setModal(createweb3Modal);
    }, [setModal]);

    const PlayGame = async () => {
        dispatch(setNftData({
            data: nftlist
        }))
        navigate('/gamePlay');
    }

    useEffect(async () => {
        if (!address) {
            navigate('/')
        } else {
            const contract = getTokenContractInstance();
            var exmocBalance000 = await contract.methods.balanceOf(address).call();
            var exmocBalance = web3.utils.fromWei(exmocBalance000, 'ether');

            setBalance(Number(exmocBalance));

            const options = {
                chain: "bsc",
                address: UserData.address,
            };

            const options2 = {
                chain: "eth",
                address: UserData.address,
            };

            let arrayNftData = [];

            console.log(options, 'options')
            const NFTS = await Web3Api.account.getNFTs(options);

            NFTS.result.map((nftData, i) => {
                const address = nftData.token_address;
                console.log(nftData, 'nftData')
                if (address === '0xfe50b96f5d673b8dc58a84930120e97f4e9b8c1d' || address === '0x5bf7576c2d25ae7e736babb5b683776a22e5db86' || address === '0x42866ef654fee1a6447c644d57b63824ec1cf521' || address === '0x188dff332d32bdc7519617249c43d790a8e2b3f4') {
                    arrayNftData.push(nftData);
                }
            })

            const ethNFTS = await Web3Api.account.getNFTs(options2);

            ethNFTS.result.map(ethNft => {
                const address = ethNft.token_address;
                if (address === '0x4B5809Bc3e6082Ac554088C94Ad39A1A73268EA8') {
                    arrayNftData.push(ethNft);
                }
            })

            if (arrayNftData.length !== 0) {
                axios.post(`${process.env.REACT_APP_BACKEND}/NFTData/getNFTData`, {
                    links: arrayNftData
                })
                    .then(result => {
                        console.log(result.data, 'result.data')
                        setNftDatas(result.data);
                        setSearchFlag(false)
                    })
                    .catch(err => {
                        console.log(err, 'err')
                    })
            } else {
                setSearchFlag(false)
            }

        }
    }, [address]);

    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <Toaster />
            <div style={{ display: 'flex', flexDirection: 'column', width: '100%', height: window.innerHeight + 'px' }}>
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginTop: '30px' }}>
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        <button className="button-85" style={{ width: '100% !important' }}>
                            <span style={{ fontSize: '20px' }}>Balance : {balance} Exmoc</span>
                        </button>
                    </div>
                </div>
                <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'center', height: '60%' }}>
                    {
                        searchFlag ?
                            <>
                                <Box style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                                    <ReactLoading type='bars' color='hsl(340deg 100% 50%)' height={'40px'} width={'100px'} />
                                </Box>
                            </>
                            :
                            nftDatas.length ?
                                nftDatas.map((nft, key) => {
                                    return (
                                        <Card className='Mobile' sx={{ backgroundColor: 'transparent', margin: '1% 0%' }} key={key}>
                                            <CardActionArea>
                                                <CardMedia
                                                    component="img"
                                                    image={nft.image}
                                                    alt="green iguana"
                                                />
                                                {/* <CardContent>
                                                            <Typography gutterBottom variant="h5" component="div">
                                                                Token Name
                                                            </Typography>
                                                            <Typography variant="body2" color="text.secondary">
                                                                These are NFT MetaDataList
                                                            </Typography>
                                                        </CardContent> */}
                                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                                    <Checkbox onClick={(e) => {
                                                        if (e.target.checked === true) {
                                                            setNftList(preState => [...preState, nft])
                                                        } else {
                                                            setNftList(nftlist.filter(item => item.image !== nft.image));
                                                        }
                                                    }} {...label} />
                                                    <span style={{ color: '#0080ff', fontWeight: '700' }}>You can use this card</span>
                                                </div>
                                            </CardActionArea>
                                        </Card>
                                    )
                                })
                                :
                                <>
                                    <span className='NoNFT'>You Have No NFT</span>
                                </>
                    }
                    {
                        searchFlag ?
                            <></>
                            :
                            <div style={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
                                <button onClick={PlayGame} className="button-85">Play the Game</button>
                            </div>
                    }
                </div>
            </div>
        </div >
    )
}
